import vGoodsList from 'common/components/plugin/vGoodsList.vue'
const mixins = {
    data() {
      return {
        tableColumn: [{
            type: 'checkbox',
            align: 'center',
            width: 50
          },
          {
            field: 'company_name',
            title: '库存组织',
            width: 200,
            showOverflow: true,
            headerClassName: () => {this.addClass()},
            slots: {
              default: ({row,rowIndex}) => {
                var dom = row.company_name
                if (row.edit && this.addType != 1) {
                  dom = <el-form-item ref="goods_name" prop={`tableData.${rowIndex}.company_id`} rules={[{required: true,trigger: "change", message: "请选择"}]} class="form-container-purchasepriceindex">
                    <el-select v-model={row.company_id} loading={this.downLoading} placeholder="请选择" filterable remote remote-method={val => this.getCompanyList(val, row)} onChange={val => this.companyRowChange(val, row, rowIndex)} onFocus={event =>this.getCompanyList('', row)}>
                      {
                        row.companyList.map((val) => {
                          return <el-option label={val.company_name} value={val.company_id} key={val.company_id}>
                            <span class={val.type==1 ? 'optionStyle': '' }>{ val.company_name }</span>
                          </el-option>
                        })
                      }
                    </el-select>
                  </el-form-item>
                }
                return [dom]
               }
            }
          },
          {
            field: 'goods_id',
            title: '商品编号',
            width: 150,
            showOverflow: true,
            className: () => {return this.addClass('cellSetBack')},
          },
          {
            field: 'factory_code',
            title: '工厂代码',
            width: 100,
            showOverflow: true,
            className: () => {return this.addClass('cellSetBack')},
          },
          {
            field: 'goods_name',
            title: '商品名称',
            width: 300,
            showOverflow: true,
            headerClassName: () => {return this.addClass()},
            slots: {
              default: ({row,rowIndex}) => {
                var dom = row.goods_name
                if (row.edit && this.addType != 1) {
                  let _params = {
                    company_id: row.company_id
                  },
                  showObj = {
                    goods_id: "商品编号",
                    goods_name: "商品名称",
                    factory_code: '工厂代码'
                  }
                  dom = <el-form-item ref="goods_name" prop={`tableData.${rowIndex}.goods_name`} rules={[{ required: true, trigger: "change", message: "请选择" }]} class="form-container-purchasepriceindex">
                    <v-goods-list ref= {`vGoodsList${rowIndex}`} vxeParams={_params}  showObj={showObj} scopes={{ row, rowIndex }} changeGoods={this.handleSelect}></v-goods-list>
                  </el-form-item>
                }
                return [dom]
               }
            }
          },
          {
            field: 'warehouse_name',
            title: '预警仓库',
            width: 200,
            showOverflow: true,
            slots: {
              default: ({row,rowIndex}) => {
                var dom = row.warehouse_name
                if (row.edit && this.addType != 1) {
                  if (!row.warehouseList) this.$set(row, 'warehouseList', [])
                  dom = <el-form-item ref="goods_name" class="form-container-purchasepriceindex">
                    <el-select v-model={row.warehouse_id} loading={this.downLoading} placeholder="请选择" onChange={val => this.changeWarehouse(val, row, rowIndex)} onFocus={event =>this.getWareHouseList(row)}>
                      {
                        row.warehouseList.map((val) => {
                          return <el-option label={val.warehouse_name} value={val.warehouse_id} key={val.warehouse_id}>
                          </el-option>
                        })
                      }
                    </el-select>
                  </el-form-item>
                }
                return [dom]
               }
            }
          },
          {
            field: 'brand_name',
            title: '品牌',
            width: 100,
            showOverflow: true,
          },
          {
            field: 'unit_name',
            title: '计量单位',
            width: 80,
            showOverflow: true,
          },
          {
            field: 'ceiling_inventory',
            title: '库存上限',
            width: 100,
            showOverflow: true,
            headerClassName: () => {return this.addClass()},
            slots: {
              default: ({row,rowIndex}) => {
                var dom = row.ceiling_inventory
                if (row.edit && this.addType != 1) {
                  dom = <el-form-item ref="price" prop={`tableData.${rowIndex}.ceiling_inventory`} rules={[{ required: true, message: "请输入" }]} class="form-container-purchasepriceindex">
                    <el-input placeholder="请输入" onInput={value=> this.linmikNumber(value, row, 'ceiling_inventory')} type="number" v-model={row.ceiling_inventory} style="width:100%"></el-input>
                  </el-form-item>
                }
                return [dom]
               }
            }
          },
          {
            field: 'lower_inventory',
            title: '安全库存',
            width: 100,
            showOverflow: true,
            headerClassName: () => {return this.addClass()},
            slots: {
              edit: ({row,rowIndex}) => {},
              default: ({row,rowIndex}) => {
                var dom = row.lower_inventory
                if (row.edit && this.addType != 1) {
                  dom = <el-form-item ref="price" prop={`tableData.${rowIndex}.lower_inventory`} rules={[{ required: true, message: "请输入" }]} class="form-container-purchasepriceindex">
                    <el-input placeholder="请输入" type="number" onInput={value=> this.linmikNumber(value, row, 'lower_inventory')} v-model={row.lower_inventory} style="width:100%"></el-input>
                  </el-form-item>
                }
                return [dom]
               }
            }
          },
          {
            field: 'remark',
            title: '备注',
            width: 200,
            showOverflow: true,
            className: 'setRemark',
            slots: {
              default: ({row,rowIndex}) => {
                var dom = row.remark
                // var dom = <el-input v-model={row.remark} readonly	></el-input>
                if (row.edit && this.addType != 1) {
                  dom = <el-input v-model={row.remark} placeholder="请输入"></el-input>
                }
                return [dom]
               }
            }
          }
        ],
        tableData: [],
      }
  },
  components: {
    vGoodsList
  },
  methods: {
    // 限制只能输入整数
    linmikNumber (value, row, key) {
      row[key] = value.replace(/[^(\-)0-9]/g,'')
    },
      // 函数返回class
    addClass(className = 'addBefore') {
      //新增修改显示必填项
      if (this.addType == 2 || this.addType == 3) {
        return className;
      } else {
        return ""
      }
    },
    // 获取选中项
    getSelectRecords() {
      let selectTion = this.$refs.xTable.getCheckboxRecords()
      selectTion = selectTion.map(item => {
        item._XID && delete item._XID
        return item
      })
      return selectTion
    },
    // 删除所有的选中行
    clearCheckboxRow() {
      this.$refs.xTable.clearCheckboxRow()
    },
    // 刷新table footer
    updateTableFooter() {
      this.$refs.xTable.updateFooter()
    },
    // 刷新行
    tableReloadRow(row) {
      this.$refs.xTable.reloadRow(row)
    },
    // 合计/页小计
    footerMethod({
      columns,
      data
    }) {
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 1) {
            return "页小计";
          }
          if (
            ["ceiling_inventory", "lower_inventory"].includes(
              column.property
            )
          ) {
            return this.$utils.sum(data, column.property);
          }
          return null;
        }),
        columns.map((column, columnIndex) => {
          if (columnIndex === 1) {
            return "合计";
          }
          if (this.TableData.allList) {
            if (column['property'] == 'ceiling_inventory') {
              return this.TableData.allList.total_ceiling_inventory;
            } else if (column['property'] == 'lower_inventory') {
              return this.TableData.allList.total_lower_inventory;
            }
          }
          return null;
        })
      ];
    }
    }
  }
  export default mixins
